import react from "react";

import {
  CCard,
  CCardHeader,
  CCardBody,
  CRow,
  CCol,
  CButton,
  CTable,
  CTableHead,
  CTableBody,
  CToaster,
  CTooltip,
} from "@coreui/react";

const months = [
  "Jan",
  "Feb",
  "Mar",
  "Apr",
  "May",
  "Jun",
  "Jul",
  "Aug",
  "Sep",
  "Oct",
  "Nov",
  "Dec",
];
function padTo2Digits(num) {
  return num.toString().padStart(2, "0");
}

function formatDate(date) {
  return [
    date.getFullYear(),
    padTo2Digits(date.getMonth() + 1),
    padTo2Digits(date.getDate()),
  ].join("-");
}

const Orders = ({ openOrders, isClose }) => {
  const ordersHeader = [
    "Time",
    "Symbol",
    "Type",
    "Side",
    "Price",
    "Amount",
    // "Filled",
    "Reduce Only",
    // "Post Only",
    // "Trigger Conditions",
    "TP/SL",
    "Cancel All",
  ];
  const curOrders = [];
  openOrders.forEach((order) => {
    console.log("openOrders.forEach", order);
    const tempOrder = [];
    const date = new Date(order.time);
    const time = `${formatDate(date)}, ${String(date.getHours()).padStart(
      2,
      "0"
    )}:${String(date.getMinutes()).padStart(2, "0")}::${String(
      date.getSeconds()
    ).padStart(2, "0")}`;
    tempOrder.push(time);
    tempOrder.push(order.symbol);
    tempOrder.push(order.type);
    tempOrder.push(order.side);
    const price = order.price === "0" ? order.stopPrice : order.price;
    tempOrder.push(Number(price).toFixed(order.pricePrecision));
    tempOrder.push(price * order.origQty);
    // tempOrder.push(order.status === "FILLED" ? "filled" : "0");
    tempOrder.push(order.reduceOnly ? "Yes" : "No");
    // tempOrder.push("--");
    // tempOrder.push("--");
    tempOrder.push("--");
    tempOrder.push("--");

    curOrders.push(tempOrder);
  });
  return (
    <>
      <CRow>
        <CCol xl={12}>
          <CCard className="mb-4">
            <CCardHeader>Open Orders ({openOrders.length})</CCardHeader>
            <CCardBody>
              <CTable className="table">
                <CTableHead>
                  <tr>
                    {ordersHeader.map((header, index) => {
                      return <th key={index}>{header}</th>;
                    })}
                  </tr>
                </CTableHead>
                <CTableBody>
                  {curOrders.map((order, index) => {
                    console.log("order " + index, order);
                    return (
                      <tr index={index}>
                        {order.map((value, idx) => {
                          return (
                            <td>
                              <span className="h6">{value}</span>
                            </td>
                          );
                        })}
                      </tr>
                    );
                  })}
                </CTableBody>
              </CTable>
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
    </>
  );
};

export default Orders;
